<template>
  <v-row class="ma-sm-4">
    <v-col cols="12">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="dates"
        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="dateRangeText"
            solo
            flat
            hide-details="auto"
            background-color="#464646"
            label="Даты создания"
            readonly
            class="rounded-lg"
            v-on="on"
          >
            <template v-slot:prepend-inner>
              <v-icon class="mr-1">
                mdi-calendar
              </v-icon>
            </template>
          </v-text-field>
        </template>
        <v-date-picker
          v-model="dates"
          no-title
          scrollable
          range
        >
          <v-spacer />

          <v-btn
            text
            color="primary"
            @click="menu = false"
          >
            Отмена
          </v-btn>
          <v-btn
            text
            color="primary"
            @click="saveDates"
          >
            ОК
          </v-btn>
        </v-date-picker>
      </v-menu>
    </v-col>

    <v-col>
      <v-card
        flat
        class="px-4 pb-2"
      >
        <v-card-title>
          <v-row>
            <v-col
              cols="12"
              sm="6"
            >
              Промокоды:
            </v-col>
            <v-col class="text-right">
              <v-dialog
                v-model="dialog"
                persistent
                max-width="500px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    depressed
                    color="primary"
                    class="text-none"
                    large
                    height="36"
                    v-bind="attrs"
                    v-on="on"
                  >
                    Создать
                  </v-btn>
                </template>

                <v-card>
                  <v-card-title>
                    <span class="text-h5">Создать промокод</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col
                          cols="12"
                          sm="6"
                        >
                          <v-text-field
                            v-model.number="amount"
                            hide-details="auto"
                            label="Сумма"
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                        >
                          <v-text-field
                            v-model.number="activationCount"
                            hide-details="auto"
                            label="Кол-во активаций"
                          />
                        </v-col>
                        <v-col cols="12">
                          <v-text-field
                            v-model.number="timeoutSec"
                            hide-details="auto"
                            label="Истекает через"
                            suffix="sec"
                          />
                        </v-col>
                        <v-col cols="12">
                          <v-text-field
                            v-model="code"
                            hide-details="auto"
                            label="Код"
                          />
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer />

                    <v-btn
                      text
                      color="primary"
                      @click="close"
                    >
                      Отмена
                    </v-btn>
                    <v-btn
                      text
                      color="primary"
                      @click="close"
                    >
                      Создать
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="[]"
          :items-per-page="5"
          no-data-text="Промокоды отсутствуют"
        >
          <template v-slot:[`item.createdAt`]="{ item }">
            {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
          </template>
        </v-data-table>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      menu: false,
      dates: [],
      start: '',
      end: '',
      dialog: false,
      amount: '',
      activationCount: '',
      timeoutSec: '',
      code: '',
      headers: [
        { text: 'Сумма:', value: 'amount', class: 'white--text text-subtitle-2' },
        { text: 'Код:', value: 'code', class: 'white--text text-subtitle-2' },
        { text: 'Кол-во активаций:', value: 'activationCounter', class: 'white--text text-subtitle-2' },
        { text: 'Макс. кол-во активаций:', value: 'activationLimit', class: 'white--text text-subtitle-2' },
        { text: 'Истекает:', value: 'expire', class: 'white--text text-subtitle-2' },
        { text: 'Дата создания:', value: 'createdAt', class: 'white--text text-subtitle-2' },
      ],
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' ~ ');
    },
  },
  methods: {
    saveDates() {
      this.$refs.menu.save(this.dates);
      [this.start, this.end] = this.dates;
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.amount = '';
        this.activationCount = '';
        this.timeoutSec = '';
        this.code = '';
      });
    },
  },
};
</script>
